import React from "react";

function CommentsList({ comments }) {
  return (
    <div>
      <h3> comments: </h3>
      {comments &&
        comments.map((comment) => (
          <div className="comment">
            <h4>{comment.username}</h4>
            <p>{comment.text}</p>
          </div>
        ))}
    </div>
  );
}

export default CommentsList;
